body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav-link {
	font-family: 'Open Sans', sans-serif;
	font-size: 16pt;
	margin-bottom: 2px;
	padding-left: 12px !important;
	padding-right: 12px !important;
}

li > a {
	color: rgba(255,255,255,.5) !important;
}

li > a:hover, li > a:active, li > .nav-item:hover {
	color: #eeeeee !important;
	margin-bottom: 0px;
	border-bottom: 2px solid #eeeeee;
}

.active {
	margin-bottom: 0px;
	border-bottom: 2px solid #eeeeee;
	color: #eeeeee !important;
}

.nav-brand {
	font-family: 'Open Sans', sans-serif;
	font-size: 16pt;
}
.centerAlign {
	align-items: center;
	justify-content: center;
	height: 100%;
	position: relative;
}

.textCenter {
	text-align: center;
}
.projectContainer {
	display: flex;
	flex-direction: column;
	align-items: center;
}
.page {
	height: 100vh;
	width: 100vw; 
	background-color: white;
	display: flex;
	flex-direction: column;
}

.headingBar {
	width: 100vw;
	height: 20vh;
	background-color: #fffbf2;
	display: flex;
}

.body {
	width: 100vw;
	background-color: white;
	display: flex;
	flex-direction: column;
	margin: 3vh 5vw 3vh 5vw;
}

.heading {
	font-family: 'Montserrat', sans-serif;
	font-weight: bold;
	font-size: 30pt;
	align-items: center;
	display: flex;
	padding-left: 5vw;
}

.leftColumn {
	width: 46%;
	margin: 1rem 2% 0 0;
}

.rightColumn {
	width: 41%;
	margin: 1rem 0 0 0;
	text-align: center;
}

.card {
	border-style: solid;
	border-color: #d3d3d3;
	-webkit-transition: all .2s ease-in-out;
	transition: all .2s ease-in-out;
}

.card:hover {
	box-shadow: 1px 1px 3px 2px #eeeeee;
	-webkit-transform: scale(1.03);
	        transform: scale(1.03);
}

.card-header {
	background-color: white;
	min-width: 220px;
	position: relative;
}

.card-header img {
	position: absolute;
	margin: auto;
	top: 0;
	bottom: 0;
}

.card-width {
	width: auto;
	max-width: 60%;
}

@media (max-width : 550px) {
	.card-width {
		width: 90%;
		max-width: 100%;
	}
}

@media (max-width : 665px) {
	.card {
		display: block;
	}

	.card-header {
		min-height: 220px;
	}

	.card-block {
		width: 100% !important;
		padding-right: 30px;
	}

	.arrowMobile {
		float: right;
		margin-bottom: 25px;
	}

	.card-header img {
		left: 0;
		right: 0;
	}
}

@media (min-width: 665px) and (max-width : 852px) {
	.card {
		max-width: 90% !important;
	}
}

@media (min-width: 852px) and (max-width : 1000px) {
	.card {
		max-width: 80% !important;
	}
}

@media (min-width: 1000px) and (max-width : 1150px) {
	.card {
		max-width: 70% !important;
	}
}

.grow { 
	-webkit-transition: all .2s ease-in-out; 
	transition: all .2s ease-in-out; 
}

.grow:hover { 
	-webkit-transform: scale(1.1); 
	        transform: scale(1.1); 
}

@media (max-width : 1000px) {
	.body .aboutContainer {
		display: block !important;
	}

	.aboutContainer .leftColumn {
		width: 90vw;
		margin-bottom: 35px;
	}

	.aboutContainer .rightColumn {
		width: 90vw;
	}
}

@media (min-width: 1000px) and (max-width : 1056px) {
	.aboutContainer .rightColumn img {
		height: 75% !important 
	}
}

.body .aboutContainer {
	display: flex;
}

.contactIconSize {
	height: 90px;
	width: 90px;
}

.contactIconMarginLeft {
	margin-left: 45px;
}

.contactIconMarginRight {
	margin-right: 45px;
}

.contactIconMarginMiddle {
	margin-left: 45px;
	margin-right: 45px;
}

@media (max-width : 392px) {
	.contactIconSize {
		height: 70px;
		width: 70px;
	}

	.contactIconMarginLeft {
		margin-left: 10px;
	}
	
	.contactIconMarginRight {
		margin-right: 10px;
	}
	
	.contactIconMarginMiddle {
		margin-left: 10px;
		margin-right: 10px;
	}
}

@media (min-width: 392px) and (max-width : 455px) {
	.contactIconSize {
		height: 70px;
		width: 70px;
	}

	.contactIconMarginLeft {
		margin-left: 40px;
	}
	
	.contactIconMarginRight {
		margin-right: 40px;
	}
	
	.contactIconMarginMiddle {
		margin-left: 40px;
		margin-right: 40px;
	}
}

@media (max-width : 410px) {
	.page h1 {
		font-size: 35px;
	}
	.page h2 {
		font-size: 22pt;
	}
	.heading {
		font-size: 25pt;
	}
}

h1 {
	font-family: 'Montserrat', sans-serif;
	font-weight: bolder;
	font-size: 40px;
}

h2 {
	font-family: 'Open Sans', sans-serif;
	font-size: 25pt;
}

h3 {
	font-family: 'Open Sans', sans-serif;
	font-size: 25pt;
	font-weight: bold;
	margin-bottom: 3vh;
}

p {
	font-family: 'Open Sans', sans-serif;
	font-size: 18pt;
}

a {
	font-family: 'Open Sans', sans-serif;
}

a:hover {
	cursor: pointer;
}

html {
	scroll-behavior: smooth;
}
